<template>
  <modal :show.sync="show" class="tk-popup-outer" @close="close">
    <template slot="header" @close="close">
      <h5 class="modal-title">Add Achievement</h5>
    </template>
    <div class="tk-popup-content">
      <base-input label="Achievement Type" class="tk-popup-selection">
        <el-select v-model="type.simple" filterable>
          <el-option
            v-for="(option, index) in type.options"
            :key="index"
            :label="option.label"
            :value="index"
          >
            <img
              class="tk-px-width-px24 tk-layout-inline-block tk-px-margin-right-px16"
              :src="option.icon"
            />
            <span class="tk-layout-inline-block">{{ option.label }}</span>
          </el-option>
        </el-select>
      </base-input>
      <span>
        <div class="form-group card-wrapper tk-px-margin-bottom-px0">
          <label class="form-control-label">Title</label>
          <div class="card tk-px-margin-bottom-px8">
            <div class="has-label card-body tk-px-padding-px0">
              <input
                type="text"
                class="form-control tk-font-color-black1"
                @keypress.enter.prevent=""
                v-model="title"
              />
            </div>
          </div>
        </div>
      </span>
      <span>
        <div class="form-group card-wrapper tk-px-margin-bottom-px0">
          <label class="form-control-label">Description</label>
          <div class="card tk-px-margin-bottom-px8">
            <div class="has-label card-body tk-px-padding-px0">
              <textarea
                class="form-control tk-font-color-black1"
                rows="5"
                resize="none"
                v-model="description"
              ></textarea>
            </div>
          </div>
        </div>
      </span>
    </div>
    <template slot="footer">
      <base-button
        type="secondary"
        @click.prevent="close"
        class="tk-font-color-lightGray tk-border-none"
        >Close</base-button
      >
      <base-button
        type="primary"
        @click.prevent="add"
        class="tk-font-color-white tk-bg-color-main tk-border-none"
        >Add</base-button
      >
    </template>
  </modal>
</template>

<script>
import $ from "jquery"
import { Select, Option } from "element-ui";
export default {
  components: {
    [Select.name]: Select,
    [Option.name]: Option,
  },
  data() {
    return {
      show: false,
      description: "",
      title: "",
      type: {
        simple: "Listening",
        icon: "/img/icons/tk/icon_hearing.png",
        options: [
          {
            label: "Listening",
            icon: "/img/icons/tk/icon_hearing.png",
          },
          {
            label: "Sight reading",
            icon: "/img/icons/tk/icon_musicSheet.png",
          },
          {
            label: "Theory",
            icon: "/img/icons/tk/icon_theory.png",
          },
          {
            label: "Song",
            icon: "/img/icons/tk/icon_song.png",
          },
          {
            label: "Creativity",
            icon: "/img/icons/tk/icon_creativity.png",
          },
          {
            label: "Group play",
            icon: "/img/icons/tk/icon_groupPlay.png",
          },
          {
            label: "Technique",
            icon: "/img/icons/tk/icon_technique.png",
          },
          {
            label: "Dedication",
            icon: "/img/icons/tk/icon_dedication.png",
          },
          {
            label: "Improvement",
            icon: "/img/icons/tk/icon_improvement.png",
          },
        ],
      },
    };
  },
  mounted() {
    let self = this;
    self.init();
    $(".tk-popup-selection").children().eq(0).addClass("card-wrapper").css({
        marginBottom: "12px"
    })
    $(".tk-popup-selection").children().eq(0).children().eq(1).addClass("card tk-px-margin-bottom-px0")
  },
  methods: {
    init() {
      setTimeout(() => {
        this.show = true;
      }, 200);
    },
    close() {
      this.$emit("close");
      this.modalClose();
    },
    add() {
      this.$emit("add");
      this.modalClose();
    },
    modalClose() {
      document.getElementsByTagName("body")[0].classList.remove("modal-open");
    },
  },
};
</script>